'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';
import '../css/listDetail.css';


var appBasePath=globalData.appBasePath;
var ListDetail=React.createClass({
	getInitialState:function(){
		return {
			activeTab: 1,
			isMask: 0,
			activeIndex:0,
			isShowDetail:false,
			loanDetail:{},
			value1:"" ,
			value2:"",
			rateMoney:""
		}
	},
	
	
	toNewsDetail:function(){
		var data = {id:3,name:"qin",age:18};
		var path = {
		  pathname:'/NewsDetail',
		  query:data,
		}
		hashHistory.push(path);
	},
	
	handleChange1: function(event) {
	    this.setState({value1: event.target.value});
	},
	handleChange2: function(event) {
	    this.setState({value2: event.target.value});
	},
	toMoneyDetail:function(){
	  	//
	  	this.setState({isShowDetail: !this.state.isShowDetail});
	},
	toProblem:function(){
	  	//var data = {};
		var path = {
		  pathname:'/Problem',
		  //query:data,
		}
		hashHistory.push(path);
	},
	toApplyInfo:function(event){
		var loanId=event.target.getAttribute("data-loanId");
	  	var data = {loanId:loanId};
		var path = {
		  pathname:'/ApplyInfo',
		  query:data,
		}
		hashHistory.push(path);
	},
	componentDidMount:function(){
		var that=this;
		var key1 = globalData.key;
		var toast=globalData.toast;
		var loanId=that.props.location.query.loanId;
		api.loanDetail(loanId,function(res){
			//console.log(res);
			if(res.code=="0000"){
				//var data=res.data;
				var data =JSON.parse(strDec(res.data,key1,"",""));
				console.log(data);
				
				var moneyMin=data.moneyMin;
				var limitMin=data.limitMin;
				var rateType=data.rateType;
				var limitType=data.limitType;
				var rate=data.rate;
				var getMyRate;
					switch (limitType){
						case "D"://
							switch (rateType){//
								case "D":
									getMyRate=rate;
									break;
								case "M":
									getMyRate=rate/30;
									break;
								case "Y":
									getMyRate=rate/365;
									break;
								default:
									break;
							}
							break;
						case "M"://
							switch (rateType){//
								case "D":
									getMyRate=rate*30;
									break;
								case "M":
									getMyRate=rate;
									break;
								case "Y":
									getMyRate=rate/12;
									break;
								default:
									break;
							}
							break;
						default:
							break;
					}
					
				//var rateMoney=
				that.setState({
					loanDetail:data,
					value1:moneyMin,
					value2:limitMin,
					myRate:getMyRate,
					isMask:data.isMask//0
				})
			}
		})
	},
	 logoError:function(event){
    	event.target.src="src/img/icon/capitalLogo.jpg";
		event.target.onerror=null; // 
		//console.log(event.target.src);
    },
    saveThis:function(event){
    	var objId=event.currentTarget.getAttribute("data-objId");
    	api.save(objId,"LOAN",function(res){
    		console.log(res);
    	})
    },
	render:function(){
		var that=this;
		//console.log("cityId",cityId);
		var loanDetail=that.state.loanDetail;
		var value1=that.state.value1*1;
		var value2=that.state.value2*1;
		var myRate=that.state.myRate*1;
		var myRateMoney=value2*myRate;
		myRateMoney=parseFloat(myRateMoney.toFixed(2)); 
		var myFeeMoney=myRateMoney+value1;
		//var myTotalMoney=myFeeMoney;
		var myTotalMoney=myFeeMoney+loanDetail.fee;
		//console.log(loanDetail);
        return (
        	<div className="app_Box listDetail">
      			<Header title={loanDetail.loanName}/>
	        	<div className="listDetailCon content">
	        		<ul className="rangeInfo">
	        			<li>
	        				<div className="numBox">
	        					
	        					<div>
	        						<input type="number"  value={that.state.value1}  onChange = {this.handleChange1}/>
	        						<span></span>
	        					</div>
	        				</div>
	        				<p>:{loanDetail.moneyMin}~{loanDetail.moneyMax}</p>
	        			</li>
	        			<li>
	        				<div  className="numBox">
	        					
	        					<div>
		        					<input type="number" value={that.state.value2}  onChange = {this.handleChange2}/>
		        					<span>{loanDetail.limitType=="D"?"":""}</span>
	        					</div>
	        				</div>
	        				<p>:{loanDetail.limitMin}~{loanDetail.limitMax}{loanDetail.limitType=="D"?"":""}</p>
	        			</li>
	        		</ul>
	        		<div className="circle">
	        			<div className="circlePic">
	        					<div className="rings">
	        						<div></div>
		        					<p>
	        							{myTotalMoney}
	        							<span></span>
	        						</p>
	        					</div>
        						
	        			</div>
	        			<ul className="circleInfo">
	        				<li><i></i> {that.state.value1}/{that.state.value2}</li>
	        				<li><i></i> {myRateMoney}({loanDetail.rate}%/{loanDetail.rateType=="D"?"":""})</li>
	        				<li><i></i> {myFeeMoney}</li>
	        				<li><i></i>{loanDetail.fee}(0%)</li>
	        			</ul>
	        		</div>
	        		<div className="moneyDetailBox">
	        			<div className="moneyDetail" style={{"display":that.state.isShowDetail?"block":"none"}}>{loanDetail.loanIntro}</div>
	        			<p onClick={that.toMoneyDetail}><img src="src/img/icon/down.png"/></p>
	        		</div>
	        		<div className="flowBox">
	        			<h2>()</h2>
	        			<div className="flowPic">
	        				<img src={loanDetail.loanFlow} onError={that.logoError}/>
	        			</div>
        				<h2></h2>
        				<div className="application">
	        				{loanDetail.loanCondition}
                          </div>    				
        				<h2></h2>
        				<div className="application">
        					{loanDetail.loanDoc}
        				</div>
	        		
	        			<h2 onClick={this.toProblem}><span><img src="src/img/icon/right.png"/></span></h2>
        				<div className="problemList">
        					<div className="problemBlock">
        						<img src="src/img/icon/problem.png"/>
        						<p>,4000,5,</p>
        						<span>:2017-09-28</span>
        					</div>
        					<div className="answerBlock">
        						<img src="src/img/icon/answer.png"/>
        						<p><span></span><span>2017-09-28</span></p>
        						<p></p>
        					</div>
        				</div>
        			</div>
	        	</div>
	        	
	        	<div className="applyBtnBox footer">
	        		<div className="applySaveBtn" data-objId={loanDetail.loanId} onClick={that.saveThis}><img src={that.state.isMask==1?"src/img/icon/sc2.png":"src/img/icon/sc1.png"} /><p>{that.state.isMask==1?"":""}</p></div>
	        		<div className="applyBtn" data-loanId={loanDetail.loanId} onClick={that.toApplyInfo}></div>
	        	</div>
        	</div>
        )
	}
});


export default ListDetail;





